import React, { Component } from 'react';

class ThemeSwitch extends Component {
  constructor(props) {
    super(props);

    this.css = `
      #___gatsby { background-image: linear-gradient(to right bottom, rgba(0,0,0, .7), rgba(0,0,0, .6)), url(../assets/bg.jpg); }
      .header { background-color: #222; }
      .header-logo { color: #fff; }
      .bm-burger-bars { background: #fff; }
      .bm-menu { background-color: #222; }
      .menu-category-item { color: #fff; }
      .btn-back { background-color: #333; }
      .menu-theme-switch { color: #fff; }
      .App { background: #222; color: #fff; }
      .bmi-page input { background-color: #222; }
      .exercise-page { background-color: #222; color: #fff; }
      .exercise-page h3 { color: #fff; }
      .exercise-path a { color: #fff; }
      .bm-menu h6 { color: #fff; }
      .menu-top-item { color: #fff; }
    `;

    this.supported = this.isDeclarationSupported('background-color', '#fff');

    this.state = {
      active: false,
      checked: false
    };

    this.toggle = this.toggle.bind(this);
  }

  isDeclarationSupported (property, value) {
    var prop = property + ':',
        el = document.createElement('test'),
        mStyle = el.style;
    el.style.cssText = prop + value;
    return mStyle[property];
  }

  toggle() {
    this.setState({
      active: !this.state.active
    });
  }

  componentDidMount() {
    if (this.props.store) {
      this.setState({
        supported: this.isDeclarationSupported(),
        active: this.props.store.getItem(this.props.storeKey)
      });
    }
  }

  componentDidUpdate() {
    if (this.props.store) {
      this.props.store.setItem(this.props.storeKey, this.state.active);
    }
  }

  render() {
    if (!this.supported) {
      return null;
    }

    return (
      <div className="switch">
        <input className="ios-switch" id="switcher" type="checkbox" aria-pressed={this.state.active} onClick={this.toggle}>
        </input>
        <label htmlFor="switcher" className="menu-theme-switch"> </label>
        <style media={this.state.active ? 'screen' : 'none'}>
          {this.state.active ? this.css.trim() : this.css}
        </style>
      </div>
    );
  }
}
if(typeof(Storage) !== "undefined"){
  ThemeSwitch.defaultProps = {
    preserveRasters: false,
    store: localStorage,
    storeKey: 'ThemeSwitch'
  }
}

export default ThemeSwitch;



