import React from "react";

class Footer extends React.Component {
  render () {
    return (
      <footer className="footer">
        <div className="app-container">
          <div className="footer-container">
            <div className="footer-legal">Fitness Map 2018 | Designed by @airsyn</div>
            <div className="footer-social">
              <a href="mailto:pojar.vladislav@gmail.com" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-envelope" />
                </svg>
              </a>
              <a href="https://github.com/airsyn" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-github" />
                </svg>
              </a>
              <a href="https://www.youtube.com/channel/UCJ0Y21f7dAdBWZmB6iuMWjQ?view_as=subscriber" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-youtube-play" />
                </svg>
              </a>
              <a href="https://soundcloud.com/airsyn" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-soundcloud" />
                </svg>
              </a>
              <a href="https://instagram.com/airsyn" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-instagram" />
                </svg>
              </a>
              <a href="https://facebook.com/airsyn" className="footer-link">
                <svg className="footer-icon">
                  <use xlinkHref="/sprite.svg#icon-facebook-with-circle" />
                </svg>
              </a>
            </div>
          </div>
        </div> 
      </footer>
   );
  }
}

export default Footer;



